export default {
  name: 'liekkas-table',
  data() {
    return {};
  },
  props: {
    showQueryBox: {
      type: Boolean,
      default: false // 
    },

    loadAnimation: {
      type: Boolean,
      default: false // 
    },

    dataList: {
      type: Array,
      default: () => {
        return []; // 
      }
    },

    border: {
      type: Boolean,
      default: true // 
    },

    emptyText: {
      type: String,
      default: '' // 
    },

    currentPage: {
      type: Number,
      default: 1 // 
    },

    pageSizesList: {
      type: Array,
      default: () => {
        return [10, 30, 50, 100]; // 
      }
    },

    pageSize: {
      type: Number,
      default: 10
    },
    layoutList: {
      type: String,
      default: 'total, sizes, prev, pager, next, jumper'
    }
  },
  methods: {
    /**
     * 
     */

    sortChange(column, prop, order) {
      const param = {
        column: column,
        prop: prop,
        order: order
      };
      this.$emit('getSortChange', param);
    },
    /**
     * checkbox
     */
    selectChange(selection, row) {
      const param = {
        selection: selection,
        row: row
      };
      this.$emit('getSelectChange', param);
    },
    /**
     * 
     */
    checkChange(selection) {
      const param = {
        selection: selection
      };
      this.$emit('getColumnCheckChange', param);
    },
    /**
     *  
     */
    clickRow(row, column) {
      const param = {
        row: row,
        column: column
      };
      this.$emit('getClickRowData', param);
    },
    /**
     * pageSize  
     */
    handleSizeChange(size) {
      this.$emit('handleSizeChange', size);
    },
    /**
     * currentPage 
     */
    handleCurrentChange(currentPage) {
      this.$emit('handleCurrentChange', currentPage);
    }
  }
};